import React, { useEffect, useState } from "react";
import { Link, useHref } from 'react-router-dom';
import $ from 'jquery';
export default function ProductWrapper({ children }) {
    const [list, setList] = useState(false);
    const path = useHref();
    useEffect(() => {

        if (path == '/') setList(false);
        if (path != '/') setList(true);
        $('.products__items').ready(function () {
            $(".products__item-link").click(function () {
                $(".products__item-link").removeClass('products__item-link--active');
                $(this).toggleClass('products__item-link--active');
            });
        });
        $(function () {
            $("a.products__item-link, .footer__up-top").click(function () {
                $("html, body").animate({
                    scrollTop: $($(this).attr("href")).offset().top - 50
                }, {
                    duration: 500,
                    easing: "swing"
                });
                return false;
            });
        });
    }, [])
    console.log(list);
    return (
        <>
            <section className="products">
                <div className="container">
                    <div class="products__items-wrapper">

                        {!list && <>
                            <ul class="products__items">
                                <li class="products__item">
                                    <a class="products__item-link products__item-link--active" href="#prod-1">
                                        
                                        
                                    </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-2"> </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-3"> </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-4"></a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-5"> </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-6"> </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-7"> </a>
                                </li>
                                <li class="products__item">
                                    <a class="products__item-link" href="#prod-8"></a>
                                </li>
                            </ul>
                        </>}
                        {list && <>
                            <ul class="products__items">
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link products__item-link--active" >
                                            
                                            
                                        </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link"> </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link"> </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link"></p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link"> </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link" > </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link to='/'>
                                        <p class="products__item-link"> </p>
                                    </Link>

                                </li>
                                <li class="products__item">
                                    <Link>
                                        <p class="products__item-link"></p>
                                    </Link>

                                </li>
                            </ul>
                        </>}

                    </div>
                    {children}
                </div>
            </section>
        </>
    )
}